* {
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
}

.game-board {
    width: 100%;
    height: 500px;
    border-bottom: 15px solid rgb(35, 160, 35);
    margin: 0px auto;
    position: relative;
    overflow: hidden;
    background: linear-gradient(#87CEEB, #E0F6FF);

    .pipe {
        position: absolute;
        bottom: 0px;
        width: 80px;
        animation: pipe-animation 2s infinite linear;
    }
    
    .mario {
        width: 150px;
        position: absolute;
        bottom: 0px;
    }
    
    .jump {
        animation: jump 500ms ease-out;
    }

    .clouds {
        width: 550px;
        position: absolute;
        animation: clouds-animation 20s infinite linear;
    }
}


@keyframes pipe-animation {
    from {
        right: -80px;
    }

    to {
        right: 100%;
    }
}

@keyframes jump {
    0% {
        bottom: 0px;
    }

    40% {
        bottom: 180px;
    }

    50% {
        bottom: 180px;
    }

    60% {
        bottom: 180px;
    }

    100% {
        bottom: 0px;
    }
}

@keyframes clouds-animation {
    from {
        right: -550px;
    }

    to {
        right: 100%;
    }
}